import React from 'react';

const NotFound404 = () => {
  return (
    <div className="text-center container">
      <i
        className="fa fa-exclamation-circle fa-5x mt-5 pt-5 text-danger"
        aria-hidden="true"
      />
      <h2>Nothing Found! Please Try Again ...</h2>
    </div>
  );
};

export default NotFound404;



